//react 
import * as React from "react";

import { Button, Modal, Toast } from 'antd-mobile'
import { sendContent } from '../../api/course'
import NoAuth from './noAuth'
import { getUserInfo } from '../../api/bind';
import empty from '../../assets/img/empty.png'
import './empty.less'

interface ISprops {
    [propName: string]: any;
}
interface IState {
    [statesName: string]: any
}

class CourseEmpty extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            modal1: false,
            textValue: '',
            user_type: '',//
            isLoading: false,
            btnDisabled: false,
            
        }
    }
    pressFlag:boolean = true;
    handleClick = () => {
        this.setState({
            modal1: true,
        })
    }
    onClose = () => {
        this.setState({
            modal1: false,
        })
    }
    submitContent = () => {
        this.pressFlag = false;
        this.onClose()
        const { textValue } = this.state;
        if (textValue) {
            sendContent({
                content: textValue,
                feed_view: '/courseList',
            }).then(res => {
                this.pressFlag = true;
                this.onClose()
                if(!res.status) {
                    Toast.info('')
                } else {
                    Toast.fail(res["message"],2)
                }
            })
        } else {
            this.pressFlag = true;
            Toast.fail("",2)
        }
        
    }
    changeValue = (e) => {
        this.setState({
            textValue: e.target.value
        })
    }
    getUser = () => {
        //
        this.setState({isLoading: true})
        getUserInfo({}).then(res => {
            this.setState({isLoading: false})
            if (!res.status) {
                this.setState({
                    user_type: res.data.user_type
                })
            }
        })
    }
    componentWillMount() {
        this.getUser()
    }
    render() {
        const { source } = this.props;
        const { modal1, textValue, user_type, isLoading } = this.state;
        const { pressFlag } = this;
        console.log(pressFlag)
        return (
            <div className="course-empty">
                <img src={empty} alt="" />
                {
                    source === 1 ?
                        <div className="empty-i">
                            <p></p>
                            <Button className="empty-btn" type="primary" onClick={this.handleClick}></Button>
                        </div>
                        :
                        user_type === 2 || user_type === 3 || isLoading ?
                            <div className="empty-i">
                                <p></p>
                            </div>
                            :
                            <div className="empty-i">
                                <p>VIP~</p>
                                <a className="empty-btn vip" href="tel:021-28872842" type="primary"></a>
                            </div>
                }

                <Modal
                    visible={modal1}
                    transparent
                    title=""
                    maskClosable={false}
                    onClose={() => this.onClose()}
                    footer={[{ text: '', onPress: () => this.onClose() },
                    { text: '', onPress: pressFlag ? this.submitContent : () => {} }]}
                >
                    <textarea onChange={(e) => this.changeValue(e)} value={textValue} className="textarea" placeholder=""></textarea>
                </Modal>

            </div>
        );
    }

}

export default CourseEmpty